// 
import juiQrcode from './qrcode/index.js'
import skuTable from './sku/index.js'

// 
const components = [
  juiQrcode,
  skuTable
  ];

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  components.map(component => Vue.component(component.name, component))
}

// script
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}


export default {
  //  install Vue.use() 
  install,
  // 
  juiQrcode,
  skuTable
}
export {
  juiQrcode,
  skuTable
}